},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"row justify-content-center align-items-center mb-2",attrs:{"data-aos":"fade-right"}},[_c('div',{staticClass:"col-lg-8 mb-3"},[_c('p',[_vm._v("En determinadas especies vegetales, las características y la calidad de los aceites esenciales están directamente relacionadas con el estado de desarrollo de la planta. Dependiendo de la especie, la recolección debe realizarse antes, durante o después del periodo de floración, ya que en cada una de estas etapas la concentración y proporción de los compuestos volátiles puede variar significativamente.")]),_c('p',{staticClass:"mb-4"},[_vm._v("De igual manera, la hora del día en que se recolectan las estructuras vegetales influye de forma determinante en la composición química del aceite esencial. Un ejemplo representativo es el caso de las flores de "),_c('em',[_vm._v("ylang-ylang")]),_vm._v(" (Cananga odorata), de las cuales se obtiene un aceite esencial de calidad extra cuando la recolección se realiza durante las primeras horas de la mañana. A medida que avanza el día, la concentración de ciertos compuestos responsables de esta alta calidad disminuye progresivamente.")]),_c('div',{staticClass:"cajon color-primario p-4 p-lg-5 h-100"},[_c('p',[_vm._v("En síntesis, para obtener aceite esencial de ylang-ylang de calidad extra (materia prima altamente valorada en la industria de la perfumería y cuyo precio puede oscilar entre 100 y 200 dólares por kilogramo) es indispensable recolectar las flores completamente maduras, identificables por su color amarillo intenso, durante las primeras horas del día. Asimismo, estas deben ser sometidas lo más pronto posible al proceso de destilación por arrastre de vapor (hidrodestilación), con el fin de preservar sus propiedades aromáticas y químicas.")])])]),_c('div',{staticClass:"col-lg-4 col-8 col-md-6"},[_c('img',{attrs:{"src":require("@/assets/curso/Tema1/img07.png")}})])])
