},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"row justify-content-center align-items-center mb-3",attrs:{"data-aos":"fade-right"}},[_c('div',{staticClass:"col-lg-5 mb-4 col-11 col-md-9"},[_c('img',{attrs:{"src":require("@/assets/curso/Tema1/img09.png")}})]),_c('div',{staticClass:"col-lg-7"},[_c('p',[_vm._v("Las características del aceite esencial obtenido están estrechamente relacionadas con los procedimientos físicos o químicos empleados durante su extracción. En muchos casos, la correcta selección y aplicación del método de extracción es un factor determinante para la calidad del producto final.")]),_c('p',[_vm._v("No todos los aceites esenciales pueden obtenerse mediante un mismo método, ya que cada planta y cada órgano vegetal presentan particularidades estructurales y químicas. Es importante señalar que únicamente los métodos de destilación y extracción mecánica permiten obtener aceites esenciales verdaderos, es decir, aquellos con un alto grado de pureza. Otros métodos dan lugar a productos aromáticos que contienen fracciones de aceite esencial acompañadas de residuos o trazas de disolventes.")])])])
